___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background-color: gainsboro;
}

header {
  background-color: whitesmoke;
  padding: 30px 0 30px 0;
}

header h1 {
  color: cadetblue;
  margin: 0 250px 0 0;
  text-align: center;
  font-size: 3.5rem;
  font-weight: 900;
}

.main-div {
  margin: 50px 0 0 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 0 30px;
}

.user-input-div h2 {
  color: maroon;
  margin: 0 0 15px 0;
}

#form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#form input {
  border-radius: 5px;
  width: 200px;
  margin: 10px 0 0 0;
  padding: 5px 10px;
  border: 2px solid black;
}

button {
  border-radius: 3px;
  margin: 10px 0 0 0;
  padding: 4px 20px;
  box-shadow: 3px 3px #000;
  font-size: 1rem;
}

button:hover {
  background-color: cadetblue;
  color: #fff;
}

button:active {
  background-color: cadetblue;
  color: #fff;
  box-shadow: none;
  transform: translateY(4px);
}

#refresh-btn {
  margin: 0 0 0 10px;
}

#form button {
  position: relative;
  left: 30%;
}

.scores-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 20px 0;
}

.scores-header h2 {
  color: maroon;
}

.scoreboard {
  width: 400px;
  border: 2px solid black;
  box-shadow: 3px 3px 5px #000;
}

.score-element {
  padding: 10px 0 10px 10px;
}

.score-item {
  padding: 5px;
}

.white-bg {
  background-color: white;
}

.grey-bg {
  background-color: rgb(240, 240, 240);
}

@media screen and (max-width: 600px) {
  header h1 {
    margin: 0 0 0 0;
    font-size: 2.5rem;
  }

  .main-div {
    flex-direction: column;
    grid-gap: 25px 0;
  }

  .user-input-div {
    order: 1;
    margin: 0 0 50px 0;
  }

  .user-input-div h2 {
    text-align: center;
  }

  .leaderboard-div {
    order: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .scoreboard {
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .score-element {
    padding: 10px 0 10px 10px;
  }

  #form button {
    position: relative;
    left: 60px;
  }

  .scores-header {
    justify-content: center;
    margin: 0 0 20px 0;
  }
}
